
.header{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1rem;
    position: fixed;
    width: 100%;
    height: 90px;
    z-index: 10;
    top:0;
}

h1 {
    color: #1B2631;
}


.header-bg {
    background-color: rgba(0, 0 , 0, 0.85);
    transition: 0.5s;
}

.clChange{
    color: white;
}

.nav-menu {
    display: flex;
}

.nav-menu li{
    padding: 0 1rem;
    list-style: none;
}

.nav-menu li a {
    font-size: 1.2rem;
    font-weight: 500;
    color: #1B2631;
}

.nav-menu.active a, .nav-menu.active{
    color: white;
}



.hamburger {
    display: none;
}

@media screen and (max-width: 1040px) {
    .nav-menu {
        flex-direction: column;
        justify-content: center;
        align-items: center;
        width: 100%;
        height: 100vh;
        background: rgba(0, 0, 0, 0.96);
        position: absolute;
        top: 0;
        left: -100%;
        z-index: -3;
    }

    .nav-menu.active {
        left: 0;
    }

    .nav-menu.active a, .nav-menu.active{
        color: white;
    }

    .nav-menu li{
        padding: 1rem 0;
    }

    .nav-menu li a {
        font-size: 2rem;;
    }

    .hamburger {
        display: initial;
    }
}



